export default  function modal_checkout() {
       // 1. SELE√á√ÉO DE ELEMENTOS
    const modal = document.getElementById("modal-compra");
    const btnTrigger = document.getElementById("quero-ser-elite");
    const btnClose = document.querySelector(".modal__close");
    const btnCheckout = document.querySelector(".btn-checkout");
    const btnIcon = document.getElementById("btn-icon-treinamento")
    const cta_faq = document.getElementById("cta_faq")
    const btn_hero__button = document.getElementById("btn_hero__button")
    const offer_mockup=document.getElementById("offer_mockup")
    const quero=document.getElementById("quero-ser-elite")
    

    // 2. FUN√á√ÉO DE PERFORMANCE (CARREGAMENTO DIN√ÇMICO)
    const loadExternalScript = (src) => {
        return new Promise((resolve, reject) => {
            // Se o script j√° existir, resolve imediatamente
            if (document.querySelector(`script[src="${src}"]`)) return resolve();
            
            const script = document.createElement('script');
            script.src = src;
            script.async = true; // N√£o trava o carregamento
            script.onload = resolve;
            script.onerror = reject;
            document.head.appendChild(script);
        });
    };

    // 3. FUN√á√ïES DE CONTROLE DO MODAL
    const abrirModal = (e) => {
        e.preventDefault();
        modal.style.display = "flex";
        document.body.style.overflow = "hidden"; // Trava scroll do fundo
        
        // Pr√©-carrega o SDK assim que o modal abre para ganhar tempo
        loadExternalScript("https://sdk.mercadopago.com/js/v2")
            .catch(err => console.error("Erro ao pr√©-carregar SDK", err));
    };

    const fecharModal = () => {
        modal.style.display = "none";
        document.body.style.overflow = "auto"; // Devolve scroll
    };

    // 4. L√ìGICA DO CHECKOUT (SIMULA√á√ÉO API)
    const handleCheckout = async () => {
        // Feedback visual imediato
        btnCheckout.innerHTML = `<span class="spinner"></span> PROCESSANDO...`;
        btnCheckout.style.pointerEvents = "none";

        try {
            // Garante que o SDK est√° dispon√≠vel
            await loadExternalScript("https://sdk.mercadopago.com/js/v2");

            // Simula o tempo de resposta da API
            setTimeout(() => {
                const overlay = document.createElement('div');
                overlay.className = 'api-simulada-overlay';
                overlay.innerHTML = `
                    <div class="api-simulada-content">
                        <img src="https://img.icons8.com/color/96/mercado-pago.png" alt="Mercado Pago">
                        <h3>Ambiente de Demonstra√ß√£o</h3>
                        <p>A API do Mercado Pago foi carregada com sucesso via script din√¢mico para garantir <strong>Performance M√°xima (Core Web Vitals)</strong>.</p>
                        <div class="status-badge">DEVELOPER MODE: ONLINE</div>
                        <button onclick="this.parentElement.parentElement.remove()" class="btn-close-api">VOLTAR AO PROJETO</button>
                    </div>
                `;
                document.body.appendChild(overlay);

                // Reset do bot√£o
                btnCheckout.innerHTML = "FINALIZAR INSCRI√á√ÉO AGORA";
                btnCheckout.style.pointerEvents = "all";
            }, 1500);

        } catch (error) {
            btnCheckout.innerHTML = "ERRO NA CONEX√ÉO";
            btnCheckout.style.pointerEvents = "all";
        }
    };

    // 5. EVENT LISTENERS
    if (offer_mockup) offer_mockup.addEventListener("click", focus);
    if (btnTrigger) btnTrigger.addEventListener("click", abrirModal);
    if (cta_faq) cta_faq.addEventListener("click", abrirModal);
    if (btn_hero__button) btn_hero__button.addEventListener("click", abrirModal);
    if (btnIcon) btnIcon.addEventListener("click", abrirModal);
    if (btnClose) btnClose.addEventListener("click", fecharModal);
    if (btnCheckout) btnCheckout.addEventListener("click", handleCheckout);
    
    //Focus
    function focus(){
        offer_mockup.style.opacity = .5;
        quero.focus()
    }

    // Fechar ao clicar fora ou ESC
    window.addEventListener("click", (e) => { if (e.target === modal) fecharModal(); });
    document.addEventListener("keydown", (e) => { if (e.key === "Escape") fecharModal(); });
}