export default function observer(){
    const observerOptions = {
    root: null,
    rootMargin: "0px",
    threshold: 0.1      // Dispara IMEDIATAMENTE ao detectar o elemento
};

const observer = new IntersectionObserver((entries) => {
    entries.forEach(entry => {
        const target = entry.target;
        
        if (entry.isIntersecting) {
            // Usa requestAnimationFrame para n√£o travar a Main Thread
            requestAnimationFrame(() => {
                target.classList.add('active');
                target.classList.add('animate');
            });

            if (target.classList.contains('reveal')) {
                observer.unobserve(target);
            }
        } else {
            // Evita remover classes de se√ß√µes que j√° passaram se n√£o for estritamente necess√°rio
            if (!target.classList.contains('reveal')) {// se der erro remova
                requestAnimationFrame(() => {
                    target.classList.remove('active');
                });
            }
        }
    });
}, observerOptions);

// Registra todos os alvos
document.querySelectorAll('.reveal, header, section, footer').forEach(el => {
    observer.observe(el);
});        

}

