import gtm from '../gtm.js';

export default function initCookies() {
    const banner = document.getElementById('cookie-banner');
    const modal = document.getElementById('cookie-modal');
    const btnAccept = document.getElementById('cookie-accept');
    const btnSettings = document.getElementById('cookie-settings');
    const btnSave = document.getElementById('cookie-save-prefs');
    
    const checkAnalytics = document.getElementById('pref-analytics');
    const checkMarketing = document.getElementById('pref-marketing');

    // --- FUN√á√ÉO AUXILIAR DE ACESSIBILIDADE ---
    // Gerencia o foco para que o teclado n√£o "caia" em elementos escondidos
    const gerenciarAcessibilidade = (container, ativo) => {
        if (!container) return;
        const elementosFocaveis = container.querySelectorAll('button, [href], input, label');
        
        if (ativo) {
            container.style.display = "flex";
            container.setAttribute('aria-hidden', 'false');
            elementosFocaveis.forEach(el => el.setAttribute('tabindex', '0'));
        } else {
            container.setAttribute('aria-hidden', 'true');
            elementosFocaveis.forEach(el => el.setAttribute('tabindex', '-1'));
            // Opcional: display none ap√≥s a anima√ß√£o (0.5s)
            setTimeout(() => {
                if (!container.classList.contains('active')) container.style.display = "none";
            }, 500);
        }
    };

    // 1. Inicializa√ß√£o: Garante que tudo comece bloqueado para o teclado
    gerenciarAcessibilidade(banner, false);
    gerenciarAcessibilidade(modal, false);

    const savedConsent = JSON.parse(localStorage.getItem('cookie-consent-v2'));

    // 2. Verifica√ß√£o imediata
    if (savedConsent) {
        if (banner) {
            banner.classList.add('hidden-force');
            banner.style.display = "none";
        }
        if (savedConsent.analytics || savedConsent.marketing) {
            gtm();
            enviarDataLayer(savedConsent);
        }
        return;
    }

    // 3. Exibi√ß√£o para novos usu√°rios
    const mostrarBanner = () => {
        if (banner) {
            gerenciarAcessibilidade(banner, true);
            setTimeout(() => banner.classList.add('active'), 10);
        }
    };

    if ('requestIdleCallback' in window) {
        window.requestIdleCallback(() => setTimeout(mostrarBanner, 3000));
    } else {
        window.addEventListener('load', () => setTimeout(mostrarBanner, 3000));
    }

    // 4. Salvamento
    const salvarConsentimento = (analytics, marketing) => {
        const consentObj = {
            analytics,
            marketing,
            date: new Date().toISOString()
        };

        localStorage.setItem('cookie-consent-v2', JSON.stringify(consentObj));
        
        if (analytics || marketing) {
            gtm();
            enviarDataLayer(consentObj);
        }

        // Fecha e bloqueia acessibilidade
        banner?.classList.remove('active');
        modal?.classList.remove('active');
        gerenciarAcessibilidade(banner, false);
        gerenciarAcessibilidade(modal, false);
        
        if (banner) banner.classList.add('hidden-force');
    };

    function enviarDataLayer(consent) {
        window.dataLayer = window.dataLayer || [];
        window.dataLayer.push({
            'event': 'cookie_consent_updated',
            'consent_analytics': consent.analytics,
            'consent_marketing': consent.marketing
        });
    }

    // 5. Eventos
    btnSettings?.addEventListener('click', () => {
        // Quando abre o modal, desativa o banner e ativa o modal
        gerenciarAcessibilidade(banner, false);
        banner?.classList.remove('active');
        
        gerenciarAcessibilidade(modal, true);
        modal?.classList.add('active');
    });

    btnSave?.addEventListener('click', () => salvarConsentimento(checkAnalytics.checked, checkMarketing.checked));
    btnAccept?.addEventListener('click', () => salvarConsentimento(true, true));
}