export function configurarRetencao() {
    const modal = document.getElementById('modal-retencao');
    const btnFicar = document.getElementById('btn-ficar');
    const btnSair = document.getElementById('btn-sair');
    
    let isModalOpen = false;

    const ativarRetencao = () => {
        // Se j√° tem a trava no hist√≥rico, n√£o faz nada
        if (window.history.state?.trava) return;

        // Cria o estado virtual "trava"
        window.history.pushState({ trava: true }, "");
        console.log("Sistema de reten√ß√£o pronto.");

        window.onpopstate = (event) => {
            // Se o usu√°rio tentou voltar e o modal n√£o est√° aberto
            if (!isModalOpen) {
                isModalOpen = true;
                modal.style.display = 'flex';

                // Importante: Empurramos o estado de novo para o modal "prender" o usu√°rio
                // Mas sem disparar o onpopstate novamente
                window.history.pushState({ trava: true }, "");
            }
        };
    };

  btnFicar.onclick = () => {
    modal.style.display = 'none';
    const msg = encodeURIComponent("Vim pela p√°gina de Elite e quero meu Diagn√≥stico de Escala. Estou cansado do operacional.");
    window.open(`https://wa.me/5511999999999?text=${msg}`, '_blank');
};

    btnSair.onclick = () => {
        isModalOpen = false;
        modal.style.display = 'none';
        window.onpopstate = null; // Mata a fun√ß√£o de interceptar
        
        // Volta dois n√≠veis: o da trava e o da p√°gina anterior real
        window.history.go(-2); 
    };

    document.addEventListener('click', ativarRetencao, { once: true });
    document.addEventListener('scroll', ativarRetencao, { once: true });
}