import '../scss/style.scss';

// 2. Importa o CSS Cr√≠tico APENAS em ambiente de desenvolvimento
if (import.meta.env.DEV) {
  import('../scss/critical.scss');
}

// Importamos as fun√ß√µes de outros arquivos (o Vite vai unir tudo)
import { configurarRetencao } from './retencao.js';
import themeToggle from './themeToggle.js';
import observar from './observar.js';
import modal_checkout from './modal_checkout.js';
import monitor_inp from './monitor_inp.js';
import gtm from './gtm.js';
import cookies from "./components/cookies";

window.addEventListener('load', () => {
    // Agora o cookies gerencia o carregamento do GTM internamente
    cookies(); 

    const tarefasFuncionais = [
        () => themeToggle(),
        () => modal_checkout(),
        () => observar(),
        () => configurarRetencao()
    ];

    const processarFila = (deadline) => {
        while (deadline.timeRemaining() > 1 && tarefasFuncionais.length > 0) {
            const tarefa = tarefasFuncionais.shift();
            tarefa();
        }
        if (tarefasFuncionais.length > 0) {
            requestIdleCallback(processarFila);
        }
    };

    if ('requestIdleCallback' in window) {
        requestIdleCallback(processarFila, { timeout: 3000 });
    } else {
        // Fallback b√°sico
        tarefasFuncionais.forEach(t => t());
    }
});